import Vue from 'vue'
import App from './App.vue'
import vuesax from 'vuesax'
import 'vuesax/dist/vuesax.css'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import VueRouter from 'vue-router';
import router from './router';
import store from './vuex/store.js';
import axios from "axios";
import 'boxicons';
import {startErrorNotification} from "@/axios/loading";
import qs from "qs";
import uploader from 'vue-simple-uploader'

Vue.use(uploader)
Vue.use(VueRouter);
Vue.use(ElementUI);
Vue.use(vuesax)
Vue.config.productionTip = false
// =========================axios拦截器==================================

// 请求拦截
axios.interceptors.request.use(config => {
    let token = localStorage.getItem("token");
    //let token = Vue.prototype.$cookie.get("token");
    if (token) {
        config.headers['token'] = token;
    }

    //设置post请求的请求头部信息, //不是上传文件的时候不修改
    if (!config.headers['isFile'] && (config.method === 'post' || config.method === 'put')) {
        config.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'
        config.data = qs.stringify(config.data);
    }
    return config;
}, error => {


    return Promise.reject(error);
})

// 响应拦截
axios.interceptors.response.use(response => {

    return response;
}, error => {

    //弹窗
    startErrorNotification(error.response.status)
    return Promise.reject(error);
})


new Vue({
    render: h => h(App),
    router,
    store: store,
    beforeCreate() {
        Vue.prototype.$bus = this
    }
}).$mount('#app')
