                //     name: 'weeklyReportList',
                //     path: 'weeklyreportlist',
                //     meta: {title: '周志'},
                //     component: WeeklyReportList,
                //     children:[
                //         {
                //             name:'oneTaskInfo',
                //             path:'onetaskinfo',
                //             component:OneTaskInfo
                //         }
                //     ]
                // },
